import { isTouchDevice } from '../../../utils';
export const DROPDOWN_DIRECTIONS_INTERNAL = ['down', 'up'];
export const DROPDOWN_DIRECTIONS = [...DROPDOWN_DIRECTIONS_INTERNAL, 'auto'];
export const isCustomDropdown = (filter, native) => {
    if (filter) {
        return true;
    }
    else if (native) {
        return false;
    }
    else {
        return !isTouchDevice();
    }
};
